body {
  background-color: #e3f2fd;
}

nav {
  padding: 20px;
  font-family: Georgia, "Times New Roman", Times, serif;
  font-size: 20px;
  background-color: cadetblue;
  margin-bottom: 10px;
}

nav a {
  display: flex;
  justify-content: baseline;
  text-decoration: none;
  color: white;
  margin-bottom: 10px;
}

div .about-me {
  font-family: Georgia, "Times New Roman", Times, serif;
}

div .about-me img {
  margin-left: 50px;
  max-height: 500px;
  max-width: 400px;
}

.projects {
  height: 100%;
}


.projectsContainer {
  width: 25%;
  padding: 10px;
  margin: 2%;
  float: left;
  justify-content: left;
  align-items: left;
  display: flex;
  flex-direction: column;
}

.projects img:hover {
  opacity: 0.5;
  zoom: 1.2;
}

.contact-me {
  font-size: 20px;
  font-family: "Times New Roman", Times, serif;
  color: black;
}

@media screen and (max-width: 860px) {
  body {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-content: space-between;
  }

  div .about-me img {
    margin-left: 1px;
    max-width: 400px;
    max-height: 300px;
  }
}
